.f-bd {
  position: relative
}
.f-bd:after {
  position: absolute;
  z-index: 2;
  content: "";
  top: 0;
  left: 0;
  pointer-events: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: 100%;
  -webkit-transform-origin: top left;
  transform-origin: top left;
  border: 0 solid rgba(0,0,0,.1)
}
.f-bd-top:after {
  border-top-width: 1px
}
.f-bd-btm:after {
  border-bottom-width: 1px
}
.f-bd-tb:after {
  border-width: 1px 0
}
.f-bd-left:after {
